import "./PartialImage.css";
import empty from "./empty.png";

const PartialImage = ({ imageSize, imageSrc, style, imgStyle }) => {
  return (
    <div style={style} className="partial__image__container">
      <img
        className="partial__image__part"
        src={imageSrc || empty}
        style={imgStyle}
        alt="Partial"
        width={imageSize}
      />
    </div>
  );
};

export default PartialImage;
